.App {
  min-height: 100vh;
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 60px; /* footer's height */
}

/* Loading */

.loading-wrapper {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  text-align: center;
}

.loading-wrapper .loading {
  width: 100px;
  height: 100px;
  border: 10px solid rgba(156, 156, 156, 0.25);
  border-left: 10px solid #00b0af;
  border-radius: 50%;
  -webkit-animation: spin 1.1s infinite linear;
          animation: spin 1.1s infinite linear;
}

.loading-wrapper .text {
  color: #00b0af;
  font-size: 14px;
  margin-top: 20px;
}



